<template>
	<section class="personal">
		<div class="forgetHead">
			<img src="../images/back_aw.png" @click="history"/>
			<span>个人信息</span>
			<!--<button>保存</button>-->
		</div>
		<div class="personalList">
			<mt-cell title="昵称" is-link to='/nickname'>
			  <span style="color: #999999; font-size:0.2rem;">{{nickname}}</span>
			</mt-cell>
			<mt-cell title="密码设置" is-link to='/modifyPass'>
			  <span style="color: #999999; font-size:0.2rem;">修改</span>
			</mt-cell>
		</div>
		<div class="next">
			<mt-button type="danger" @click='next'>退出登录</mt-button>
		</div>
	</section>
  
</template>
<script>
	import { MessageBox, Toast } from 'mint-ui'
	import { logout, getUser } from './../api/api'
	import {mapMutations} from 'vuex'
	import { removeStore, getStore } from './../config/mUtils'
export default {
  data () {
    return {
    	phone: '', // 手机号
    	nickname: null // 昵称
    	
    }
  },
  methods: {
  	...mapMutations([
		'OUT_LOGIN'
	]),
  	history() { // 后退
		this.$router.push({path: '/mine'})
	},
	next() {
		MessageBox.confirm('', {
			title: '',
			message: '确定退出'
		}).then(action => {
		  	logout().then(res => {
				if (res.code === 200) {
					Toast({
						message: res.msg,
						duration: 2000
					});
					this.OUT_LOGIN()
					this.$router.push({path: '/mine'})
					removeStore('token')
					removeStore('userId')
				} else if (res.code === 413) {
					Toast({
						message: res.msg,
						duration: 2000
					});
					this.$router.push({path: '/login'})
				}
			})
		});
	},
	init() {
		var userId = getStore('userId')
		let params = {
			userId: userId
		}
		getUser(params).then(res => {
			if (res.code === 200) {
				this.nickname = res.data.realName
			}
		})
	}
  },
  mounted() {
  	this.init()
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
.personal {
	flex:1;
	z-index: 11;
	background: #f3f5f8;
	
	.forgetHead {
		width: 100%;
		background: white;
		position: relative;
		height: 0.8rem;
		line-height: 0.8rem;
		
		img {
			vertical-align: middle;
			width: 0.15rem;
			margin-left: 0.2rem;
		}
		span:nth-child(2) {
			vertical-align: middle;
			font-size: 0.25rem;
			color: black;
			display: inline-block;
			width: 90%;
			text-align: center;
		}
		button:nth-child(3) {
			position: absolute;
			outline: none;
			border: 0;
			background: white;
			right: 0.2rem;
			top: 0.24rem;
			font-size: 0.25rem;
		}
	}
	.personalList {
		width: 100%;
		margin-top: 0.2rem;
		
		.mint-cell {
			height: 0.8rem;
			min-height: 0.8rem;
			
			.mint-cell-wrapper {
				width: 100%;
				border-top:0.01rem solid rgba(0,0,0,0.1);
				border-bottom: 0.01rem solid rgba(0,0,0,0.1);
				.mint-cell-value {
					margin-right: 0.3rem;
				}
				.mint-cell-text {
					margin-left: 0.1rem;
				}
			}
			
		}
		.mint-cell:nth-child(2) {
			margin-top: 0.2rem;
		}
		.mint-cell-text {
			font-size: 0.25rem;
		}
		.mint-cell-allow-right::after {
			width: 0.15rem;
			height: 0.15rem;
			border: 0.02rem solid #c8c8cd;
			border-bottom-width: 0;
			border-left-width: 0;
			right: 0.2rem;
		}
	}
	.next {
		.mint-button {
			width: 80%;
			height: 0.7rem;
			margin-left: 10%;
			margin-top: 3rem;
			border-radius: 0.5rem;
			font-size: 0.25rem;
		}
	}
	
}
.mint-msgbox {
	
	.mint-msgbox-content {
		height: 1rem;
		min-height: 1rem;
		.mint-msgbox-message {
			height: 1rem;
			line-height: 1rem;
			font-size: 0.25rem;
			color: black;
		}
	}
	.mint-msgbox-btns {
		height: 0.6rem;
		line-height: 0.6rem;

		.mint-msgbox-btn {
			font-size: 0.25rem;
		}
		.mint-msgbox-confirm {
			color: #b74545;
		}
		.mint-msgbox-cancel {
			color: #999999;
		}
	}
}
</style>



